<template>
  <div class="fixed-size text-center" @click="toggleOwned" @touchend="removeHoverEffect" :style="{ width: size, height: size }" :class="{ 'not-owned': !character.owned }">
    <b-img :src="imageSrc" alt="character-icon" class="character-image" fluid></b-img>
    <div class="role-label"><b-img :src="require(`@/assets/icon/${character.role}.png`)" fluid /></div>
    <div class="element-label"><b-img :src="require(`@/assets/icon/${character.element}.png`)" fluid /></div>
    <div v-if="showLabel" class="name-label">{{ character.name }}【{{character.title}}】</div>
  </div>
</template>

<script>
export default {
  props: {
    character: Object,
    size: String,
    showLabel: Boolean 
  },
  computed: {
    imageSrc() {
      // ID を 3 桁に zero-padded してファイル名を生成
      const imageName = this.character.id.toString().padStart(3, '0') + '.jpg';
      // 画像ファイルが存在しない場合、デフォルト画像を使用する
      return require(`@/assets/character/${imageName}`) || require('@/assets/character/000.jpg');
    }
  },
  methods: {
    toggleOwned() {
      this.$emit('update-character', { ...this.character, owned: !this.character.owned });
    },
    removeHoverEffect() {
      const nameLabel = this.$el.querySelector('.name-label');
      if (nameLabel) {
        nameLabel.classList.remove('hover');
      }
    }
  }
};
</script>

<style scoped>
.fixed-size {
  position: relative;
  padding: 0;
  margin: 0;
  overflow: hidden;
  cursor: pointer;
}
.fixed-size:hover {
  background-color: rgb(169, 169, 172);
  transition-duration: 0.2s;
  transition-property: all;
  transition-timing-function: ease-in;
}
.character-image {
  display: block;
  border-radius: 50%; /* 円形にする */
  overflow: hidden;
  width: 100%;
}
.not-owned{
 filter: grayscale(100%) brightness(50%);
}

.element-label {
  position: absolute;
  top: 0;
  right: 0;
  width: 30%;
}
.role-label {
  position: absolute;
  top: 0;
  left: 0;
  width: 30%;
}
.element-label > img,
 .role-label > img{
  display: block;
  position: relative;
}


.name-label {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* 半透明の背景色 */
  color: white;
  text-align: center;
  padding: 2px 0;
  font-size: 0.8rem;
  
  user-select: none;
  /* テキストを1行で表示し、省略記号を付ける */
  white-space: nowrap; /* 折り返しを無効にする */
  overflow: hidden; /* はみ出した部分を隠す */
  text-overflow: ellipsis; /* はみ出した部分を「...」にする */
  /* ホバー時の設定 */
  transition: all 0.3s ease; /* アニメーション効果を追加 */
}

.fixed-size:hover .name-label,
.fixed-size .name-label.hover {
  white-space: normal; /* テキストを折り返す */
  overflow: visible; /* はみ出した部分を表示 */
  text-overflow: clip; /* テキストの切り捨てを解除 */
  background-color: rgba(0, 0, 0, 0.8); /* 背景色を少し濃くする */
  padding: 2px 0px; /* パディングを調整して見やすくする */
}
</style>
